/* Dm Sans Start */

/* Regular */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Regular.ttf');
    font-weight: normal;
    font-style: normal;
}

/* Regular Italic */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Italic.ttf');
    font-weight: normal;
    font-style: italic;
}

/* Medium */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Medium.ttf');
    font-weight: 500;
    font-style: normal;
}

/* Medium Italic */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-MediumItalic.ttf');
    font-weight: 500;
    font-style: italic;
}

/* Bold */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Bold.ttf');
    font-weight: 700;
    font-style: normal;
}

/* Bold Italic */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-BoldItalic.ttf');
    font-weight: 700;
    font-style: italic;
}

/* Dm Sans End */

/* Lato Start */

/* Thin */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Thin.ttf');
    font-weight: 100;
    font-style: normal;
}

/* Thin Italic */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-ThinItalic.ttf');
    font-weight: 100;
    font-style: italic;
}

/* Light */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Light.ttf');
    font-weight: 300;
    font-style: normal;
}

/* Light Italic */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-LightItalic.ttf');
    font-weight: 300;
    font-style: italic;
}


/* Regular */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Regular.ttf');
    src: local('Lato-Regular'), local('Lato-Regular'),
    url('../fonts/Lato/Lato-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

/* Regular Italic */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Italic.ttf');
    font-weight: 400;
    font-style: italic;
}

/* Bold */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Bold.ttf');
    font-weight: 700;
    font-style: normal;
}

/* Bold Italic */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-BoldItalic.ttf');
    font-weight: 700;
    font-style: italic;
}

/* Black */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-Black.ttf');
    font-weight: 900;
    font-style: normal;
}

/* Black Italic */
@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato/Lato-BlackItalic.ttf');
    font-weight: 900;
    font-style: italic;
}


/* Lato End */


